.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  position: relative;
  box-shadow: 0px 15px 8px 0px rgba(0, 0, 0, 0.5);
}

.logo {
  display: flex;
  justify-content: center;
}
.logo .logo__img {
  width: 132px;
  position: absolute;
  top: -4px;
  z-index: -1;
}

.navigation .link {
  margin-right: 20px;
  font-size: 24px;
}
.navigation .link:last-of-type {
  margin: 0;
}

.hero1 {
  background-image: url("../img/switches.webp");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 317px;
  overflow: hidden;
  display: grid;
  place-items: center;
}
.hero1 .hero1__text {
  color: #FFFFFF;
  font-size: 40px;
  margin: 20px;
}

.providers {
  padding: 20px;
}
@media (max-width: 769px) {
  .providers .providers__title {
    text-align: center;
  }
}

.providers__imgs {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(10rem, 1fr));
  place-items: center;
  gap: 112px;
  margin: 15px;
}
.providers__imgs .provider {
  width: 220px;
  height: auto;
}